/* ***
  Global
 *** */
$global-font-color: #112233;
$global-header-bg-color: #112233;
$global-header-font-color: #ffffff;
$global-bg-color: #dadad9;
$global-font-family: "Fira Sans Condensed", sans-serif;
$global-btn-bg-color: #886f68;
$global-btn-font-color: #ffffff;
$global-textbox-border-color: #d7cecb;
$global-footer-font-color: #b3a394;
$global-footer-bg-color: #0d1726;
$global-footer-link-hover-color: #bb0a21;

/* ***
  Configure match UI
 *** */
$server-textbox-lbl-bg-color: #6d213c;
$server-textbox-lbl-font-color: #ffffff;
$server-textbox-border-color: hsl(339, 54%, 90%);

$receiver-textbox-lbl-bg-color: $global-header-bg-color;
$receiver-textbox-lbl-font-color: $global-header-font-color;
$receiver-textbox-border-color: hsl(210, 50%, 90%);

/* ***
  Actual match UI
 *** */
$match-srv-rcv-bg-color: $server-textbox-lbl-bg-color;
$match-player-bg-color: $receiver-textbox-lbl-bg-color;
$match-font-color: $global-header-font-color;
$match-timer-bg-color: $match-srv-rcv-bg-color;
$match-icon-bg-color: #d7cecb;

/* ***
  Tournament fixture UI
 *** */
$tmt-stage-summary-bg-color: $server-textbox-lbl-bg-color;
$tmt-stage-summary-hover-bg-color: hsl(339, 54%, 34%);
$tmt-stage-summary-border-color: #b3a394;

$tmt-plain-match-bg-color: #ccceca;
$tmt-plain-match-grp-head-font-color: #fff;
$tmt-plain-match-grp-head-bg-color: #6e8087;
$tmt-plain-match-marker-font-color: #fff;
$tmt-plain-match-marker-bg-color: hsl(197, 10%, 28%);
$tmt-match-team-bg-color: #fff;
$tmt-standing-grp-head-font-color: $server-textbox-lbl-bg-color;
$tmt-standing-grp-head-bg-color: #ccc5b9;
$tmt-standing-marker-font-color: #fff;
$tmt-standing-marker-bg-color: #887a63;

$tmt-pool-name-head-font-color: #fff;
$tmt-pool-name-head-bg-color: $global-header-bg-color;
