@import "../main.scss";

/* ***
  Server/Receiver player input
*** */
.serving > div,
.receiving > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.serving > div > label,
.receiving > div > label {
  width: 100%;
  margin: 0px;
  padding: 0.38em;
}

.serving > div > label {
  color: $server-textbox-lbl-font-color;
  background-color: $server-textbox-lbl-bg-color;
}

.receiving > div > label {
  color: $global-header-font-color;
  background-color: $global-header-bg-color;
}

.serving > div > input,
.receiving > div > input {
  @include general-textbox();
  margin-top: 0px;
  margin-bottom: 0.5em;
}

.serving > div > input {
  border-color: $server-textbox-border-color;
}

.receiving > div > input {
  border-color: $receiver-textbox-border-color;
}

/* ***
  Game options and button styles
 *** */
.game-options,
.buttons {
  @include flex-h-v-center();
}

.game-options > .score_to_win_items {
  display: flex;
  align-items: center;
  padding: 16px;
}

.game-options > .score_to_win_items > label {
  width: 100%;
  margin: 0px;
  padding: 0.25em;
}

.game-options > .score_to_win_items > input {
  @include general-textbox();
  padding: 4px;
}
